body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 0 auto;
    padding: 10%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    background-color: rgb(144, 230, 74);
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    font-weight: bold;
    font-size: 2em;
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 10px solid #999;
    float: left;
    font-size: 3em;
    font-weight: bold;
    line-height: 100x;
    height: 100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 100px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }

  .square:hover {
      background-color: aqua;
  }